	module.exports = {"data":{"markdownRemark":{"html":"<h2 id=\"overview\"><a href=\"#overview\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Overview</h2>\n<p>Always add a modifier class. Do not use <code>.pf-c-alert</code> on it's own.</p>\n<h2 id=\"accessibility\"><a href=\"#accessibility\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Accessibility</h2>\n<p><code>role=\"alert\"</code> is used to bring immediate attention to the user.  </p>\n<h2 id=\"usage\"><a href=\"#usage\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Usage</h2>\n<table>\n<thead>\n<tr>\n<th>Class</th>\n<th>Usage</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><code>.pf-c-alert</code>\n \n<strong>Applied to:</strong>\n \n<code>&#x3C;div></code></td>\n<td><strong>Outcome:</strong>\n Applies default alert styling \n<strong>Required:</strong>\n Yes \n<strong>Remarks:</strong>\n Always use with a modifier class</td>\n</tr>\n<tr>\n<td><code>.pf-is-success</code>\n \n<strong>Applied to:</strong>\n \n<code>&#x3C;div></code></td>\n<td><strong>Outcome:</strong>\n Applies success styling \n<strong>Required:</strong>\n No \n<strong>Remarks:</strong></td>\n</tr>\n<tr>\n<td><code>.pf-is-danger</code>\n \n<strong>Applied to:</strong>\n \n<code>&#x3C;div></code></td>\n<td><strong>Outcome:</strong>\n Applies danger styling \n<strong>Required:</strong>\n No \n<strong>Remarks:</strong></td>\n</tr>\n<tr>\n<td><code>.pf-is-warning</code>\n \n<strong>Applied to:</strong>\n \n<code>&#x3C;div></code></td>\n<td><strong>Outcome:</strong>\n Applies warning styling \n<strong>Required:</strong>\n No \n<strong>Remarks:</strong></td>\n</tr>\n<tr>\n<td><code>.pf-is-info</code>\n \n<strong>Applied to:</strong>\n \n<code>&#x3C;div></code></td>\n<td><strong>Outcome:</strong>\n Applies info styling \n<strong>Required:</strong>\n No \n<strong>Remarks:</strong></td>\n</tr>\n</tbody>\n</table>","fields":{"path":"/components/docs/docs","type":"documentation","contentType":"component"}}},"pathContext":{"pagePath":"/components/docs/docs","type":"documentation","contentType":"component"}}

